.pathfinding-container {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 50px auto 50px;
}

.boxes-container {
    display: grid;
    grid-template-columns: repeat(40, 20px);
    grid-template-rows: repeat(20, 20px);
    position: relative;
    text-align: center;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    user-select: none;
}

.left-container,
.right-container {
    width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.left-container div div {
    display: flex;
    align-items: center;
}

.left-container h3 {
    cursor: pointer;
}

.left-container p {
    margin-top: 0;
}

.right-container svg,
.right-container img {
    width: 110px;
    height: 100px;
}

.highlighted-drawing-mode {
    transform: scale(1.2);
}
