.algo-overview {
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
}

.intro-container {
    max-width: 45%;
}

.intro-container p {
    font-size: 1rem;
    margin: 0;
    padding: 0;
}

.complexity-container {
    text-align: right;
    position: relative;
    width: 30rem;
    height: fit-content;
    z-index: 1;
}

.complexity-container h3 {
    display: inline-block;
    padding: 5px 10px;
    border-radius: 10px;
    box-shadow: 0 0 25px rgba(255, 255, 255, 0.6);
    background-color: var(--bg-color);
}

.complexity-box {
    position: absolute;
    text-align: left;
    width: 30rem;
    padding-left: 10px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.9);
    box-shadow: 0 0 25px rgba(255, 255, 255, 0.6);
}

.complexity-box:hover {
    cursor: none;
}

.complexity-box h4 {
    margin: 7px 0 10px;
}
