.box {
    background-color: white;
    margin: 2px;
    cursor: pointer;
}

.legend-box {
    background-color: white;
    width: 20px;
    height: 20px;
    margin-right: 20px;
}

.box:hover {
    transform: scale(1.25);
}

.starting-node {
    background-color: lime !important;
}

.ending-node {
    background-color: red !important;
}

.wall-node {
    background-color: rgb(54, 54, 54);
}

.visited-node {
    animation: animate-visited 1s linear alternate;
    background-color: rgb(161, 161, 161);
}

.shortest-path {
    animation: animate-shortest 1s linear alternate;
    background-color: rgb(00, 0, 255);
}

@keyframes animate-visited {
    from {
        transform: scale(0);
    }
    70% {
        transform: scale(1.2);
    }
    to {
        transform: scale(1);
        background-color: rgb(161, 161, 161);
    }
}

@keyframes animate-shortest {
    from {
        background-color: rgb(225, 0, 255);
        transform: scale(0);
        filter: blur(2px);
    }
    70% {
        transform: scale(1.33);
    }
    to {
        transform: scale(1);
        background-color: rgb(00, 0, 255);
    }
}
