.modal-wrapper {
    position: absolute;
    width: 100%;
    height: 1500px;
    overflow: hidden;
}

.modal {
    z-index: 100;
    position: absolute;
    top: 10vh;
    left: 20%;
    width: 60%;
    background: white;
    border-radius: 15px;
    background: var(--bg-color);
    box-shadow: 0 0 25px rgba(255, 0, 0, 0.6);
    animation: dynamicShadow 10s 5s linear infinite alternate;
}

.modal__header {
    width: 100%;
    padding: 2rem 4rem;
    color: white;
    display: flex;
    align-items: center;
}

.modal__header h1 {
    font-size: 3rem;
    width: 85%;
}

.modal__header img {
    width: 15%;
    height: 15%;
    border-radius: 100%;
    box-shadow: 0 0 25px rgba(255, 0, 0, 0.6);
    animation: dynamicShadow 10s 5s linear infinite alternate;
}

.modal__content {
    position: relative;
    padding: 1rem 0.5rem;
    width: 100%;
}

.modal__content .left {
    width: 60%;
    margin: auto auto 50px 25px;
}

.modal__content .right {
    width: 60%;
    margin: auto 20px auto auto;
}

.modal__content h3 {
    font-size: 1.5rem;
    margin-top: 10px;
}

.modal__content p {
    font-size: 1rem;
    margin: 0;
}

.modal__footer {
    padding: 1rem 0.5rem;
    width: 100%;
    text-align: center;
}

.modal__footer button {
    cursor: pointer;
    background-color: transparent;
    color: white;
    border: none;
    box-shadow: 0 0 25px rgba(255, 0, 0, 0.6);
    animation: dynamicShadow 10s 5s linear infinite alternate;
    padding: 5px 15px;
    font-size: 20px;
    border-radius: 5px;
    margin: 40px auto;
}

.modal__footer button:hover {
    filter: brightness(2);
}

.modal-enter {
    transform: translateY(-10rem);
    opacity: 0;
}

.modal-enter-active {
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
}

.modal-exit {
    transform: translateY(0);
    opacity: 1;
}

.modal-exit-active {
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
}
