.algo-head-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.dropdown-arrow {
    width: 50px;
    margin-bottom: 10px;
}

.algo-selection {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 1000;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1%;
}

.algo-head-container h2,
.algo-head-container h3 {
    cursor: pointer;
}

.algo-head-container h3 {
    font-size: 3rem;
    color: rgb(190, 190, 190);
    text-decoration: underline;
}

.algo-head-container div {
    margin-bottom: 3rem;
}

.algo-head-container h2:hover {
    transform-origin: center;
    animation: reactive 200ms ease-out forwards;
}

.help {
    cursor: pointer;
    border: 2px solid white;
    box-shadow: 0 0 35px rgba(255, 255, 255, 0.6);
    height: 50px;
    width: 50px;
    font-size: 2rem;
    border-radius: 100px;
    text-align: center;
}

@keyframes reactive {
    to {
        transform: scale(1.25) translateX(150px);
    }
}
